
.BackToTopContainer{
position: absolute;
right:2em;
}

.BackToTop {
    width: 100%;
    align-items: center;
    height: 20px;
    color: #ff8362;
    justify-content: center;
    z-index: 1000;
    cursor: pointer;
    animation: fadeIn 0.3s;
    transition: opacity 0.4s;
    opacity: 0.9;
    font-size: 0.5em;
  }
  
  .BackToTop:hover{
    opacity: 1;
  }
  
  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 0.5;
    }
  }

  @media screen and (max-width: 768px) {
    .BackToTopContainer{
      right:0.8em;
      }}

      @media screen and (max-width: 480px) {
        .BackToTopContainer{
          right:0.6em;
          max-width: 30px;
          }}
