.main-title {
    letter-spacing: 0.3em;
    margin-left: 1em;
    width: 100px;
    font-size: 60px;
    color: #fff;
    font-family: "roboto";
    font-weight: 200;
}

.title-bold {
    font-weight: 300;
}

#Hero {
    background-image: url("../images/Cityscape.jpg");
    grid-area: Hero;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
  }
  

  @media screen and (max-width: 1200px) {

    .main-title {
        font-size: 50px;
        margin-left: 0.5em;
    }
} 
